<template>
  <a-modal 
    :bordered="false"
    :visible="visible"
    :destroyOnClose="true"
    :title="$t('dict.title')"
    @cancel="visible=false"
    width="800px"
  >
    <a-spin :spinning="loading">
      <a-form :form="form">
        <a-form-item
          :label="$t('dict.type')"
          v-bind="formItemLayout"
        >
          <a-input 
            :placeholder="$t('dict.type_placeholder')"
            :disabled="true"
            v-decorator="[
              'dictType',
              {initialValue: data.dictType}
            ]"
          />
        </a-form-item> 
        <a-form-item
          :label="$t('dict.code')"
          v-bind="formItemLayout"
        >
          <a-input 
            :placeholder="$t('dict.code_placeholder')"
            :disabled="true"
            v-decorator="[
              'dictCode',
              {initialValue: data.dictCode}
            ]"
          />
        </a-form-item> 
        <a-form-item
          :label="$t('dict.description')"
          v-bind="formItemLayout"
        >
          <a-input 
            :placeholder="$t('dict.description_placeholder')"
            v-decorator="[
              'dictName',
              {initialValue: data.dictName, rules:[{required: true}]}
            ]"
          />
        </a-form-item> 
        <a-form-item
          :label="$t('dict.v1')"
          v-bind="formItemLayout"
        >
          <a-input 
            :placeholder="$t('dict.v1_placeholder')"
            v-decorator="[
              'v1',
              {initialValue: data.v1}
            ]"
          />
        </a-form-item> 
        <a-form-item
          :label="$t('dict.v2')"
          v-bind="formItemLayout"
        >
          <a-input 
            :placeholder="$t('dict.v2_placeholder')"
            v-decorator="[
              'v2',
              {initialValue: data.v2}
            ]"
          />
        </a-form-item> 
        <a-form-item
          :label="$t('dict.v3')"
          v-bind="formItemLayout"
        >
          <a-input 
            :placeholder="$t('dict.v3_placeholder')"
            v-decorator="[
              'v3',
              {initialValue: data.v3}
            ]"
          />
        </a-form-item> 
        <a-form-item
          :label="$t('dict.v4')"
          v-bind="formItemLayout"
        >
          <a-input 
            :placeholder="$t('dict.v4_placeholder')"
            v-decorator="[
              'v4',
              {initialValue: data.v4}
            ]"
          />
        </a-form-item> 
        <a-form-item
          :label="$t('dict.remark')"
          v-bind="formItemLayout"
        >
          <a-textarea 
            :rows="4" 
            :placeholder="$t('dict.remark_placeholder')"
            v-decorator="[
              'remark',
              {initialValue: data.remark}
            ]"
          />
        </a-form-item> 
      </a-form>
    </a-spin>
    <template slot="footer">
      <a-button @click="visible=false" >{{ this.$t('common.btn_cancel') }}</a-button>
      <a-button @click="onSubmit" v-show="opType=='2'" type="primary">{{ $t('common.btn_ok') }}</a-button>
    </template>
  </a-modal>
</template>

<script>
import { axios } from '@/utils/request'
export default {
  data() {
    return {
      loading: false,
      visible: false,
      form: this.$form.createForm(this),
      opType: '',
      data: {},
      formItemLayout: {labelCol: {lg: {span: 6}, sm: {span: 24}},wrapperCol: {lg: {span: 14}, sm: {span: 24}}},
    }
  },
  methods: {
    showModal(params){
      this.data = {}
      if(!params || !params.opType || params.dictType==undefined || params.dictCode==undefined){
        return
      }
      this.opType = params.opType
      this.data.dictType = params.dictType
      this.data.dictCode = params.dictCode
      this.initData(params.opType=='0'?'/sys/dict/detail':'/sys/dict/update-load')
      this.visible = true
    },
    initData(url){
      const that = this
      that.loading = true
      axios({
        url: url,
        method: 'get',
        params: {dictType: that.data.dictType, dictCode: that.data.dictCode}
      }).then(response=>{
        that.loading = false
        that.data = response.result
      })
    },
    onSubmit(){
      this.form.validateFields((err,values) => {
        if (!err) {
          const that = this
          that.loading = true
          that.data = Object.assign(that.data,values)
          axios({
            url: '/sys/dict/update',
            method: 'post',
            params: that.data
          }).then(result=>{
            that.loading = false
            if(result){
              this.$emit('ok')
            }
            this.visible = false
          })
        }
      })
      
    }
  }
}
</script>