<template>
  <div>
    <div :bordered="false">
      <transition name="fade">  
        <a-card style="background-color: #fff8db;margin-bottom: 10px;" v-show="show">
          <div class="table-page-search-wrapper">
            <a-form v-if="reset_flag" layout="inline">
              <a-row :gutter="48">
                <a-col :md="6" :sm="24">
                  <a-form-item 
                    :label="$t('dict.type')"
                  >
                    <a-select v-model="queryParam.dictType" :placeholder="$t('dict.type_placeholder')">
                      <a-select-option v-for="(text, key) in dictTypes" :key="key" :value="key">{{ text }}</a-select-option>     
                    </a-select>
                  </a-form-item>
                </a-col>
                <a-col :md="6" :sm="24">
                  <a-form-item 
                    :label="$t('dict.code')"
                  >
                    <a-input v-model="queryParam.dictCode" :placeholder="$t('dict.code_placeholder')"/>
                  </a-form-item>
                </a-col> 
                <a-col :md="6" :sm="24">
                  <a-form-item 
                    :label="$t('dict.description')"
                  >
                    <a-input v-model="queryParam.dictName" :placeholder="$t('dict.description_placeholder')"/>
                  </a-form-item>
                </a-col> 
                <a-col :md="6" :sm="24">
                  <btn-search @click="$refs.table.refresh()" :showInput="false"></btn-search>
                  <a-button @click="onReset" style="margin-left: 8px">{{ $t('common.btn_reset') }}</a-button>
                </a-col>
              </a-row>
            </a-form>
          </div>
        </a-card>
      </transition>
      <a-card>
        <div class="table-page-search-wrapper">
          <a-row>
            <a-col :md="19" :sm="19">
              
            </a-col>
            <a-col :md="5" :sm="5">
              <sorter @refresh="$refs.table.refresh()" @show="show = !show"></sorter>
            </a-col>
          </a-row>
          <s-table
            style="margin-top:10px;"
            ref="table"
            :bordered="false"
            :autoLoad="true"
            :data="loadData"
            :rowKey="record => {return record.dictType + record.dictCode;}"         
          >
            <a-table-column 
              title="#"
              width="50px"
              align="center"
            >
              <span slot-scope="text">
                <a-icon type="appstore" theme="twoTone" class="icon" style="font-size:25px"/>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('dict.type')"
              width="150px"
              dataIndex="dictType"
            ></a-table-column>
            <a-table-column
              :title="this.$t('dict.code')"
              width="150px"
              dataIndex="dictCode"
            ></a-table-column>
            <a-table-column
              :title="this.$t('dict.description')"
              width="150px"
              dataIndex="dictName"
            ></a-table-column>
            <a-table-column
              :title="this.$t('dict.v1')"
              width="200px"
              dataIndex="v1"
            ></a-table-column>
            <a-table-column
              :title="this.$t('dict.v2')"
              width="200px"
              dataIndex="v2"
            ></a-table-column>
            <a-table-column
              :title="this.$t('dict.v3')"
              width="200px"
              dataIndex="v3"
            ></a-table-column>
            <a-table-column
              :title="this.$t('dict.v4')"
              width="200px"
              dataIndex="v4"
            ></a-table-column>
            <a-table-column
              :title="this.$t('dict.action')"
              dataIndex="action"
            >
              <span slot-scope="text,record">
                <btn-edit @click="onUpdate(record)" type="a"></btn-edit>
                <a-divider type="vertical" />
                <btn-detail @click="onDetail(record)" type="a"></btn-detail>
              </span>
            </a-table-column>
          </s-table>
        </div>
      </a-card>
    </div>
    <dict-edit-modal 
      ref="dictEditModal"
      @ok="$refs.table.refresh()" 
    ></dict-edit-modal>
  </div>
</template>
<script>
  import { axios } from '@/utils/request'
  import STable from '@/components/table/'
  import DictEditModal from '@/views/sys/dict/DictEditModal'
  export default {
    name: 'Index',
    components: {
      STable,
      DictEditModal
    },
    data() {
      return {
        show: true,
        reset_flag: true,
        queryParam: {
          dictType: '',
          dictCode: '',
          dictName: ''
        },
        dictTypes: [],
        loadData: (parameter) => {
          return axios({
            url: '/sys/dict/search-list',
            method: 'get',
            params: Object.assign(parameter, this.queryParam)
          }).then(data=>{       
            return data.result
          })
        }
      }
    },
    mounted(){
      this.loadTypes()
    },
    methods: {
      onReset(){
        const that = this
        that.queryParam = {
          dictType: '',
          dictCode: '',
          dictName: ''
        }
        that.reset_flag = false
        this.$nextTick(() => {
          that.reset_flag = true
        })
      },
      loadTypes() {
        const that = this
        axios({
          url: '/sys/dict/search-type-combo',
          method: 'get',
          params: {}
        }).then(data=>{    
          that.dictTypes = data.result
        })
      },
      onDetail(item) {
        this.$refs.dictEditModal.showModal({opType: '0', dictType: item.dictType, dictCode: item.dictCode})
      },
      onUpdate(item) {
        this.$refs.dictEditModal.showModal({opType: '2', dictType: item.dictType, dictCode: item.dictCode})
      }
    }
  }
</script>
